
import Link from 'next/link'
import Layout from '../components/Layout'

const fetchRecipe = async () => {
	let recipes = await fetch('https://jsonplaceholder.typicode.com/todos/1')
  	return recipes.json()
}

const getRecipeInfo = () => (
	<ul>
		{fetchRecipe().map((item)=>
			<div>{item.title}</div>
			)
		}
	</ul>
)
export default  ()=>(
	<Layout>
	<div>
		{getRecipeInfo()}
	</div>
	</Layout>
)